import {useLocalStorage} from "@uidotdev/usehooks";
import useSearchParams from "./useSearchParams";
import {useEffect} from "react";

const useMetaPixelId = () => {
  const [pixelID, savePixelID] = useLocalStorage("pixelID", null);
  const p = useSearchParams("p");
  useEffect(() => {
    if (Boolean(!pixelID && p)) {
      savePixelID(p);
    }
  }, [p, pixelID, savePixelID]);
  return pixelID || p || null;
};

export default useMetaPixelId;